import React from 'react'

import {
    Button,
    Grid,
    Popover,
    Typography
} from '@material-ui/core'

import colors from '../../styles/theme'

export const DynamicAbility = (props) => {
    const {
        ability,
        abilityIndex,
        maxLevel,
        selectLevel,
        helperText = false,
        editable = false
    } = props

    const maxAbilityLevel = 20

    const [anchorEl, setAnchorEl] = React.useState(null)
    const open = Boolean(anchorEl)
    const handleHoverOn = (event) => { setAnchorEl(event.currentTarget) }
    const handleHoverOff = () => { setAnchorEl(null) }

    return (
        <>
        <Grid container>
            <Grid item xs={1}>
                <img onMouseEnter={handleHoverOn} 
                    style={styles.image}
                    src={ability.imageURL}
                    alt={`ability ${ability.name}`} />
            </Grid>
            <Grid item xs>
                <Grid container>
                    <Grid item xs={12}>
                        <Typography style={styles.name}>
                            {ability.type} - {ability.name} 
                            { helperText ? ` (max: ${maxLevel})` : '' }
                            { helperText
                                ? ability.type === 'Special Ability' ? ' Unlocked at 1, 9, 17'
                                : ability.type === 'Ultimate' ? ' Unlocked at 5, 13, 20'
                                : ''
                                : ''
                            }
                        </Typography>
                    </Grid>
                    { ability && [...Array(maxAbilityLevel)].map((_, index) =>
                        <Button disabled = {!editable && ability.type === 'Special Ability' && index === 0}
                            style={ (ability.type === "Special Ability" && index === 0) 
                                ? styles.specialAbilityLevelOne  
                                : ability.levels.includes(index+1) 
                                    ? styles.levelSelected 
                                    : styles.levelDefault}
                            onClick={() => 
                                selectLevel(abilityIndex, index+1, !ability.levels.includes(index+1))
                            }
                            key={`ability-${ability.name}-${index}-${ability.levels.includes(index+1)}`}>
                            {index+1}
                        </Button>
                    )}
                </Grid>
            </Grid>
        </Grid>
        <Popover
                //id={id}
                open={open}
                anchorEl={anchorEl}
                onClick={handleHoverOff}
                onClose={handleHoverOff}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}>
                <Grid container style={styles.popoverWrapper}>
                    <Grid container direction="row">
                        <Grid item xs style={{paddingLeft: 10}}>
                            <img width={75} height={75} src={ability.imageURL} />
                        </Grid>
                        <Grid item xs={10}>
                            <Grid column>
                            <Grid column>
                                <Grid item xs={12}>
                                    <h4 style={{padding: 1, margin: 1}}>{ability.name}</h4>
                                </Grid>

                                <Grid item xs={12}>&nbsp;</Grid>

                                <Grid item xs={12}>
                                    {ability.description}
                                </Grid>
                            </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
           </Popover>
        </>
    )
}

export default DynamicAbility

const styles = {
    image: {
        border: `1px solid ${colors.theme.goldPrimary}`,
        height: 75,
        width: 75
    },
    specialAbilityLevelOne: {
        backgroundColor: colors.theme.silver,
        color: colors.theme.black,
        
        minWidth: 0,
        margin: 5,
        width: 35
    },
    levelDefault: {
        backgroundColor: colors.theme.black,
        color: colors.theme.white,
        border: `1px solid ${colors.theme.goldPrimary}`,
        
        minWidth: 0,
        margin: 5,
        width: 35
    },
    levelSelected: {
        backgroundColor: colors.theme.goldPrimary,
        color: colors.theme.black,
        
        minWidth: 0,
        margin: 5,
        width: 35
    },

    popoverWrapper: {
        padding: 10,
        width: '40vw',
        border: `2px solid ${colors.theme.goldBackground}`,
        borderRadius: '5px 5px',
        color: colors.theme.white,
        backgroundColor: colors.theme.siteBackground
    }
}