import React from 'react'

import {
    Grid, 
    Popover,
    Typography
} from '@material-ui/core'

import colors from '../../styles/theme'

export const StaticAbility = (props) => {
    const {
        ability
    } = props
    
    const [anchorEl, setAnchorEl] = React.useState(null)
    const open = Boolean(anchorEl)
    const handleHoverOn = (event) => { setAnchorEl(event.currentTarget) }
    const handleHoverOff = () => { setAnchorEl(null) }

    return (
        <>
        <Grid container>
            <Grid item xs={12}>
                <Grid container>
                    <Grid item xs={2}>
                        <img onMouseEnter={handleHoverOn} 
                            style={styles.image}
                            src={ability.imageURL} 
                            alt={`ability ${ability.name}`} />
                    </Grid>
                    <Grid item xs>
                        <Grid container>
                            <Grid item xs={12}>
                                <Typography style={styles.name}>
                                    {ability.name}
                                </Typography>
                            </Grid>
                            <Grid item xs={12}>
                                <Typography style={styles.slot}>
                                    {ability.slot}
                                </Typography>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
        <Popover
                //id={id}
                open={open}
                anchorEl={anchorEl}
                onClick={handleHoverOff}
                onClose={handleHoverOff}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}>
                <Grid container style={styles.popoverWrapper}>
                    <Grid container direction="row">
                        <Grid item xs style={{paddingLeft: 10}}>
                            <img width={75} height={75} src={ability.imageURL} />
                        </Grid>
                        <Grid item xs={10}>
                            <Grid column>
                                <Grid item xs={12}>
                                    <h4 style={{padding: 1, margin: 1}}>{ability.name}</h4>
                                </Grid>

                                <Grid item xs={12}>&nbsp;</Grid>

                                <Grid item xs={12}>
                                    {ability.description}
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
           </Popover>
        </>
    )
}

export default StaticAbility

const styles = {
    image: {
        border: `1px solid ${colors.theme.goldPrimary}`,
        height: 75,
        width: 75
    },
    name: {
        fontSize: 15,
        color: colors.theme.white
    },
    slot: {
        fontSize: 10,
        color: colors.theme.white
    },

    popoverWrapper: {
        padding: 10,
        width: '40vw',
        border: `2px solid ${colors.theme.goldBackground}`,
        borderRadius: '5px 5px',
        color: colors.theme.white,
        backgroundColor: colors.theme.siteBackground
    }
}