import React, { useState } from 'react';

import BuildView from '../../../components/BuildView'

import {
  Button,
  Grid,
  Typography
} from '@material-ui/core'

import { colors } from '../../../styles/theme'

import { saveBuild } from '../../../state/actions/ethereal.actions'

const FragmentBuildReview = (props) => {
  const {
    data,
    editAction
  } = props

  const [buttonDisabled, setButtonDisabled] = useState(false)

  const save = async () => {
    setButtonDisabled(false) // true
    const response = await saveBuild(data)

    //console.log(response)
  }

  return (
    <Grid container 
      alignContent='center'
      alignItems='center'>
        <Grid item xs={12}>
          <h2 style={styles.previewBuildHeader}>
            Preview Build
          </h2>
        </Grid>
        <Grid item xs={12}>
          <BuildView data={data} editAction={editAction} /*saveAction={save} */ />
        </Grid>
    </Grid>
  )
}

export default FragmentBuildReview;

const styles = {
  header: {
    color: colors.gray
  },
  saveButton: {
    backgroundColor: colors.theme.goldBackground
  }
}