import React, { useState, useEffect } from 'react';

import {
  Button,
  Grid,
  TextField,
  Typography
} from '@material-ui/core'

import CollapsiblePairingSection from '../../../components/CollapsiblePairingSection'
import TextListButtonPicker from '../../../components/Builds/TextListButtonPicker'

import {
  getMythList
} from '../../../state/actions/ethereal.actions'

import { makeStyles } from '@material-ui/core/styles'
import { colors } from '../../../styles/theme'

const FragmentBuildPairings = (props) => {
  const {
    data,
    setData
  } = props

  const pairings = {
    threats: 1,
    synergies: 2 
  }

  const threatLevels = ["MINOR", "SMALL", "EVEN", "LARGE", "MAJOR"]
  const synergyLevels = ["ZERO", "SOME", "DECENT", "GOOD", "GREAT"]

  const [myths, setMyths] = useState([])
  const [selectedPairingType, setSelectedPairingType] = useState(1)
  const [pairingData, setPairingData] = useState(data.threats)
  const [count, setCount] = useState(0)

  useEffect(() => {
    const retrieveMyths = async () => {
      const mythList = await getMythList()
      setMyths(mythList.sort((a, b) => (a.name > b.name) ? 1 : -1).map((myth) => {
        return {
          name: myth.name,
          image: myth.backgroundURLs[0]
        }}))
    }

    retrieveMyths()
  }, [])

  const addSection = () => {
    let tempData = data
    if(selectedPairingType === 1) {
      const id = Math.max(...tempData.threats.map(s => s.id), 0)
      tempData.threats = data.threats.concat({id: id + 1})

      setPairingData(tempData.threats)
    } else {
      const id =  Math.max(...tempData.synergies.map(s => s.id), 0)
      tempData.synergies = data.synergies.concat({id: id + 1})

      setPairingData(tempData.synergies)
    }

    setData(tempData)
    setCount(count+1)
  }

  const deleteSection = (sectionId) => {
    let tempData = data

    if(selectedPairingType === pairings.threats) {
      tempData.threats = data.threats.filter(s => s.id !== sectionId)
      setPairingData(tempData.threats)
    } else {
      tempData.synergies = data.synergies.filter(s => s.id !== sectionId)
      setPairingData(tempData.synergies)
    }

    setData(tempData)
    setCount(count+1)
  }

  const togglePairing = () => {
    setSelectedPairingType(selectedPairingType === pairings.threats ? pairings.synergies : pairings.threats)
    setPairingData(selectedPairingType === pairings.threats ? data.synergies : data.threats)
  }

  const savePairingMyth = (index, myth) => {
    let tempData = data

    if(selectedPairingType === 1) {
      tempData.threats[index].myth = myth
    } else {
      tempData.synergies[index].myth = myth
    }

    setData(tempData)
    setCount(count+1)
  }

  const savePairingLevel = (index, level) => {
    let tempData = data

    if(selectedPairingType === 1) {
      tempData.threats[index].level = level
    } else {
      tempData.synergies[index].level = level
    }

    setData(tempData)
    setCount(count+1)
  }

  const saveNotes = (index, notes) => {
    let tempData = data
    if(selectedPairingType === 1) {
      tempData.threats[index].notes = notes
    } else {
      tempData.synergies[index].notes = notes
    }

    setData(tempData)
  }

  const useStyles = makeStyles({
    root: {
      "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
        border: `1px solid ${colors.theme.grey}`,
        color: colors.theme.white
      },
      "& .MuiOutlinedInput-input": {
        padding: 12
      }
    },
    
    input: {
      color: colors.theme.white
    },
  })

  const classes = useStyles()

  return (
    <Grid container 
      alignContent='center'
      alignItems='center'>
        <Grid container style={{ justifyContent: 'center' }}>
          <Grid item xs={1}>
            <Button onClick={togglePairing}
              style={ selectedPairingType === pairings.threats ? styles.activePairing : styles.inactivePairing }>
                THREATS
              </Button>
          </Grid>
          <Grid item xs={1}>
          <Button onClick={togglePairing}
              style={ selectedPairingType === pairings.synergies ? styles.activePairing : styles.inactivePairing }>
                SYNERGIES
              </Button>
          </Grid>
        </Grid>
      <Grid item xs={12}>
        <Button style={styles.buttonAddSection}
          onClick={() => addSection()}>
            ADD A { selectedPairingType === pairings.threats ? "THREAT" : "SYNERGY" }
        </Button>
      </Grid>
      { pairingData.map((section, sIndex) => 
        <CollapsiblePairingSection 
          key={`sections-${selectedPairingType === pairings.threats ? "threats" : "synergies"}-${section.id}`}
          style={styles.section}
          sectionIndex={sIndex}
          sectionId={section.id}
          title={section.title}
          defaultExpanded={pairingData.length === 1}
          data={section}
          levels={selectedPairingType === pairings.threats ? threatLevels : synergyLevels }
          deleteSection={() => deleteSection(section.id)}>
          <Grid container>
            { myths.map((myth, index) => 
              <Grid item xs>
                <Button onClick={() => savePairingMyth(sIndex, myth)}
                    style={{opacity: section?.myth?.name === myth.name ? 1 : 0.5 }}>
                      <svg xmlns="http://www.w3.org/2000/svg" width="90" height="90" viewBox="0 0 62 62">
                        <defs>
                          <clipPath id="mythClipPath">
                            <path d="M212.771,337.46l-8.666,10.5v49.5h51.334l8.666-10.5v-49.5Z" 
                            transform="translate(-203.605 -336.96)" />
                          </clipPath>
                        </defs>
                        <image href={myth?.image} width={120} height={61} x={-27} y={0} clipPath="url(#mythClipPath)" />
                        <path d="M212.771,337.46l-8.666,10.5v49.5h51.334l8.666-10.5v-49.5Z" 
                            transform="translate(-203.605 -336.96)" stroke={colors.theme.goldPrimary} fill="none" strokeWidth="1" />
                      </svg>
                  </Button>
              </Grid>
            )}
            <Grid item xs />
            { section.myth && 
              <Grid container>
                  <Grid item xs={12}>
                    <Typography style={styles.pairingMessage}>
                      { selectedPairingType === pairings.threats
                        ? `How much of a threat is ${section.myth.name}?`
                        : `How compatible is ${section.myth.name}?`
                      }
                    </Typography>
                  </Grid>
                  <Grid item xs={12} >
                    <TextListButtonPicker 
                      activeText={section.level}
                      textList={selectedPairingType === pairings.threats ? threatLevels : synergyLevels}
                      setData={savePairingLevel}
                      sectionId={sIndex}
                      justifyContent={"start"}
                      />
                  </Grid>
                <Grid item xs />
                <Grid item xs={12}>
                  
                </Grid>
              </Grid>
            }
            <Grid container>
              <Grid item xs={12}>
                <TextField multiline rows={6} 
                  variant="outlined"
                  style={styles.notes} 
                  className={ classes.root }
                  InputProps={{
                    className: classes.input
                  }}
                  placeholder="Notes..."
                  defaultValue={section.notes}
                  onChange={(e) => { 
                    saveNotes(sIndex, e.target.value)
                  }} />
              </Grid>
            </Grid>
          </Grid>
        </CollapsiblePairingSection>
      )}
      <Grid item xs={12}>
        <Button style={styles.buttonAddSection}
          onClick={() => addSection()}>
            ADD A { selectedPairingType === pairings.threats ? "THREAT" : "SYNERGY" }
        </Button>
      </Grid>
    </Grid>
  )
}

export default FragmentBuildPairings;

const styles = {
  header: {
    color: colors.gray
  },
  section: {
    marginBottom: 10
  },
  buttonAddSection: {
    backgroundColor: colors.theme.goldPrimary,
    fontSize: "0.7em",
    marginTop: 10,
    marginBottom: 15
  },

  activePairing: {
    background: colors.theme.goldPrimary,
    fontSize: "0.7em",
    width: 40
  }, 
  inactivePairing: {
    color: colors.theme.white,
    background: colors.theme.sectionBackground,
    fontSize: "0.7em",
    width: 40
  },

  pairingMessage: {
    color: colors.theme.white
  },

  notes: {
    width: "100%",

    marginTop: 15
  }
}