import React from 'react'

import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Button,
  Grid,
  Typography
} from '@material-ui/core'

import SectionHeaderPairing from './Builds/SectionHeaderPairing'

import { makeStyles } from '@material-ui/core/styles'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faTrashAlt
} from '@fortawesome/free-solid-svg-icons'

import colors from '../styles/theme'

import ExpandMoreIcon from '@material-ui/icons/ExpandMore'

const CollapsiblePairingSection = (props) => {
    const {
      data,
      levels,
      sectionIndex,
      sectionId,
      deleteSection,
      defaultExpanded
    } = props

    const useStyles = makeStyles({
      root: {
        "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
          border: `1px solid ${colors.theme.grey}`,
          color: colors.theme.white
        },
        "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
          border: `1px solid ${colors.theme.grey}`,
          color: colors.theme.white
        },
        "& .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline": {
          border: `1px solid ${colors.theme.grey}`,
          color: colors.theme.white
        },
        "& .MuiOutlinedInput-input": {
          padding: 12
        }
      },
      
      input: {
        color: colors.theme.white
      },
    })
  
    const classes = useStyles()

    return (
      <Grid container
        style={styles.section}
        alignContent='center'
        alignItems='center'>
        <Grid item xs={12}>
          <Accordion style={{borderRadius: 5}} 
            defaultExpanded={defaultExpanded}>
            <AccordionSummary
              style={styles.summary}
              expandIcon={<ExpandMoreIcon style={{ fill: colors.theme.white }} />} >
              <Grid container
                onClick={(event) => event.stopPropagation()}
                onFocus={(event) => event.stopPropagation()} 
                alignItems="center"
                direction="row"
                >
                <Grid item xs>
                  { data.myth && 
                  <Grid container style={styles.sectionHeaderPairingsContainer}>
                    <Grid item xs={1}>
                      <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" viewBox="0 0 62 62">
                        <defs>
                          <clipPath id="mythClipPath">
                            <path d="M212.771,337.46l-8.666,10.5v49.5h51.334l8.666-10.5v-49.5Z" 
                            transform="translate(-203.605 -336.96)" />
                          </clipPath>
                        </defs>
                        <image href={data.myth?.image} width={120} height={61} x={-27} y={0} clipPath="url(#mythClipPath)" />
                        <path d="M212.771,337.46l-8.666,10.5v49.5h51.334l8.666-10.5v-49.5Z" 
                            transform="translate(-203.605 -336.96)" stroke={colors.theme.goldPrimary} fill="none" strokeWidth="1" />
                      </svg>
                    </Grid>
                    <Grid item xs={1}>
                      <Typography style={{ color: colors.theme.white }}>{data.myth.name}</Typography>
                    </Grid>
                    <Grid item xs={6}>
                      { data.level &&
                      <Button disabled={true}
                        style={styles.selectedButton}
                      >{data.level}</Button>
                      }
                    </Grid>
                  </Grid>
                  }
                </Grid>
                <Grid item xs={1}
                  direction="row-reverse">
                    <Button 
                        style={styles.deleteSectionButton}
                        onClick={() => deleteSection(sectionId)}>
                      <Grid container
                        direction="column"
                        style={styles.faTrashIcon}>
                        <Grid item>
                          <FontAwesomeIcon
                          styles={styles.faTrashIcon}
                          icon={faTrashAlt} color="white" size="lg" />
                        </Grid>
                        <Grid item
                          style={styles.label}>
                          Delete
                        </Grid>
                      </Grid>
                  </Button>
                </Grid>
              </Grid>
            </AccordionSummary>
            <AccordionDetails style={styles.details}>
              {props.children}
            </AccordionDetails>
          </Accordion>
        </Grid>
      </Grid>
    )
  }
  
export default CollapsiblePairingSection;


const styles = {
  section: {
    marginBottom: 10
  },
  deleteSectionButton: {
    margin: 0,
    padding: 0,
    color: colors.theme.white
  },
  sectionHeaderPairingsContainer: {
    justifyContent: 'flex-start',
    alignItems: 'center'
  },
  sectionHeaderItem: {
    paddingLeft: 10
  },
  summary: {
    backgroundColor: colors.theme.sectionHeaderBackground,

    height: 65
  },
  details: {
    backgroundColor: colors.theme.sectionBackground,
  },
  label: {
    color: colors.theme.white,
    fontSize: "0.8em"
  },
  faDragIcon: {
    alignContent: "center",
    alignItems: "center",
    justify: "center"
  },
  faTrashIcon: {
    alignContent: "center",
    alignItems: "center",
    justify: "center"
  },

  selectedButton: {
    color: colors.theme.black,
    backgroundColor: colors.theme.goldPrimary,
    fontSize: "0.7em",
    marginTop: 10,
    marginBottom: 15
  }
}