import React from 'react'

import {
    Grid,
    Popover,
    Typography
} from '@material-ui/core'

import colors from '../../styles/theme'

export const Item = (props) => {
    const {
        item,
        section,
        setData
    } = props

    const optionsCursorTrueWithMargin = {
        followCursor:true,
        shiftX:-20,
        shiftY:-20
    }

    const [anchorEl, setAnchorEl] = React.useState(null)
    const open = Boolean(anchorEl)
    const handleHoverOn = (event) => { setAnchorEl(event.currentTarget) }
    const handleHoverOff = () => { setAnchorEl(null) }

    return (
        <>
            <Grid container
                onMouseEnter={handleHoverOn}
                //onMouseLeave={handleHoverOff}
                onClick={() => setData(section, item)}
                direction="column"
                justify="center"
                alignItems="center"
                style={styles.item}>
                <Grid item xs={2}
                    style={{
                        backgroundImage: `url(${item.imageURL})`,
                        backgroundSize: "100%",
                        backgroundRepeat: "no-repeat",
                        minWidth: 75,
                        minHeight: 75,
                        height: "100%",
                        width: "100%",
                    }}>
                </Grid>
                <Grid container style={{ textAlign: 'center' }}>
                    <Grid item xs />
                    <Grid item xs={2}>
                        <img src={`${process.env.PUBLIC_URL}/images/currency_gold.png`} width={10} height={10} alt="currency - gold" />
                    </Grid>
                    <Grid item xs={5} style={styles.itemCost}>
                        <Typography>
                            {item.cost}
                        </Typography>
                    </Grid>
                    <Grid item xs />
                </Grid>
                <Grid item xs
                    style={styles.itemName}>
                    {item.name}
                </Grid>
             </Grid>
             <Popover
                //id={id}
                open={open}
                anchorEl={anchorEl}
                onClick={handleHoverOff}
                onClose={handleHoverOff}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}>
                <Grid container style={styles.popoverWrapper}>
                    <Grid container direction="row">
                        <Grid item xs style={{paddingLeft: 10}}>
                            <img width={75} height={75} src={item.imageURL} />
                        </Grid>
                        <Grid item xs={10}>
                            <Grid column>
                                <Grid item xs={12}>
                                    <h4 style={{padding: 1, margin: 1}}>{item.name}</h4>
                                </Grid>

                                <Grid item xs={12}>
                                    Total Price: {item.cost}
                                </Grid>

                                <Grid item xs={12}>&nbsp;</Grid>
                                
                                { item.stats.map((stat, index) => 
                                    <Grid item xs={12}>
                                        {stat}
                                    </Grid>
                                )}
                                <Grid item xs={12}>&nbsp;</Grid>
                                { item.effects && item.effects.map((effect, index) => 
                                    <>
                                        <Grid item xs={12} key={`item-${item.id}-effect-${index}`}>
                                            {effect.title.length > 0 && `${effect.title}: `}{effect.detail}
                                        </Grid>
                                        <Grid item xs={12}>&nbsp;</Grid>
                                    </>
                                )}
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
           </Popover>
        </>
    )
}

export default Item

const styles = {
    item: {
        cursor: "pointer"
    },
    itemName: {
        color: colors.theme.white,
        textAlign: 'center',
        fontSize: 12
    },
    itemCost: {
        background: `1px solid ${colors.theme.goldBackground}`,

        margin: 0,
        padding: 0
    },

    popoverWrapper: {
        padding: 10,
        width: '40vw',
        border: `2px solid ${colors.theme.goldBackground}`,
        borderRadius: '5px 5px',
        color: colors.theme.white,
        backgroundColor: colors.theme.siteBackground
    }
}