import React from 'react'

import {
    Button,
    Grid
} from '@material-ui/core'

import { colors } from '../../styles/theme'

const TextListButtonPicker = (props) => {
    const {
        activeText,
        textList,
        setData,
        sectionId,
        justifyContent
    } = props

    return (
        <Grid container style={{ justifyContent: justifyContent }}>
            { textList.map((text, _) => 
                <Grid item xs={1} >
                    <Button disabled={setData === undefined ? true : false}
                        style={text === activeText ? styles.selectedButton : styles.button}
                        onClick={() => setData(sectionId, text)}>{text}</Button>
                </Grid>
            )}
        </Grid>
    )
}

export default TextListButtonPicker

const styles = {
    selectedButton: {
        color: colors.theme.black,
        backgroundColor: colors.theme.goldPrimary,
        fontSize: "0.7em",
        marginTop: 10,
        marginBottom: 15
    },
    button: {
        color: colors.theme.goldPrimary,
        border: `1px solid ${colors.theme.goldPrimary}`,
        fontSize: "0.7em",
        marginTop: 10,
        marginBottom: 15
    }
}