import React from 'react'

import {
    Grid,
    Popover
} from '@material-ui/core'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faFileAlt
} from '@fortawesome/free-solid-svg-icons'

import colors from '../styles/theme'

export const PopoverNotes = (props) => {
    const {
        notes
    } = props

    const [anchorEl, setAnchorEl] = React.useState(null)
    const open = Boolean(anchorEl)
    const handleHoverOn = (event) => { setAnchorEl(event.currentTarget) }
    const handleHoverOff = () => { setAnchorEl(null) }

    return (
        <>
            <FontAwesomeIcon onMouseEnter={handleHoverOn}
                styles={styles.faTrashIcon}
                icon={faFileAlt} color="white" size="lg" /> 
            <Popover
                id={`popover-notes`}
                open={open}
                anchorEl={anchorEl}
                onClick={handleHoverOff}
                onClose={handleHoverOff}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}>
                <Grid container style={styles.popoverWrapper}>
                    <Grid item xs={12}>
                        {notes}
                    </Grid>
                </Grid>
            </Popover>
        </>
    )
}

export default PopoverNotes

const styles = {
    popoverWrapper: {
        padding: 10,
        width: '40vw',
        border: `2px solid ${colors.theme.goldBackground}`,
        borderRadius: '5px 5px',
        color: colors.theme.white,
        backgroundColor: colors.theme.siteBackground,
        whiteSpace: 'pre-wrap'
    }
}